.Footer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-direction: row;
        flex-direction: row;
    margin: 2rem -1rem;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.Footer-item{
    margin: 1rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    color: black;
    text-decoration: none;
    border-bottom: 1px solid transparent;
}


.Footer-item:hover{
    border-bottom: 1px solid black;
}

.Footer-item--logo{
    height: 1rem;
    width: 1rem;
    margin-right: .5rem; 
}


@media screen and (max-width: 500px)
{
    .Footer{
        max-width: 300px;
    }
}






