.History{

}

.History-title{
    font-size: 2rem;
    font-weight: bold;
    text-align: left;
}

.History-items{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.History-item{
