    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    margin-right: 2rem; 
}


.History-item-picture{
    max-width: 80px;
    height: auto;
    -ms-flex-item-align: center;
        align-self: center;
    padding: 1rem .5rem;
}

.History-item-info{
    display: -ms-flexbox;
