}

.History-item-info--date{
    padding: .5rem;
    margin-top: -1rem;
    color: #aaa;
}

@media screen and (max-width: 750px)
{
    .History-items{
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .History-item{
